/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { compose } from '@wordpress/compose';
import { useState } from '@wordpress/element';
import { useSelect, useDispatch, dispatch } from '@wordpress/data';
import { useEntityProp } from '@wordpress/core-data';
import { chevronDown } from '@wordpress/icons';
import {
	Button,
	DropZone,
	Icon,
	Spinner,
	TextControl,
	withNotices,
} from '@wordpress/components';
import {
	MediaUpload,
	MediaUploadCheck,
} from '@wordpress/block-editor';

/**
 * Internal dependencies
 */
import PublishGuideItem from '../publish-guide-item';
import { constant } from '@wpex/nextgen-common';
const { EID_PREFIX } = constant;

/**
 * Module constants
 */
const ALLOWED_MEDIA_TYPES = [ 'image' ];

export const SiteInfoGuide = ( props ) => {
	const {
		isOpen,
		toggleComplete,
		toggleOpen,
		noticeOperations,
		noticeUI,
	} = props;

	const [ title, setTitle ] = useEntityProp( 'root', 'site', 'title' );
	const [ description, setDescription ] = useEntityProp( 'root', 'site', 'description' );

	const [ logoUrl, setLogoUrl ] = useState();
	const [ logoIsSaving, setLogoIsSaving ] = useState( false );

	/* istanbul ignore next */
	const { mediaItemData, sitelogo, onDropImage } = useSelect( ( select ) => {
		const siteSettings = select( 'core' ).getEditedEntityRecord( 'root', 'site' );
		const mediaItem = select( 'core' ).getEntityRecord( 'root', 'media', siteSettings.sitelogo );

		return {
			mediaItemData: mediaItem && {
				url: mediaItem.source_url,
				alt: mediaItem.alt_text,
			},
			sitelogo: siteSettings.sitelogo,
			onDropImage: ( filesList ) => {
				select( 'core/block-editor' )
					.getSettings()
					.mediaUpload( {
						allowedTypes: ALLOWED_MEDIA_TYPES,
						filesList,
						onFileChange: ( [ image ] ) => onSelectLogo( image ),
						onError( message ) {
							noticeOperations.removeAllNotices();
							noticeOperations.createErrorNotice( message );
						},
					} );
			},
		};
	}, [] );

	const { editEntityRecord } = useDispatch( 'core' );
	const setLogo = ( newValue ) =>
		editEntityRecord( 'root', 'site', undefined, {
			sitelogo: newValue,
		} );

	const onSelectLogo = ( media ) => {
		if ( ! media ) {
			return;
		}

		if ( ! media.id && media.url ) {
			setLogo( '' );
			setLogoUrl( media.url );
		}

		if ( media.id ) {
			setLogo( media.id.toString() );
		}
	};

	const deleteLogo = () => {
		setLogo( '' );
		setLogoUrl( '' );
	};

	// Save the new settings.
	const saveSettings = () => {
		return dispatch( 'core' ).saveEntityRecord( 'root', 'site', {
			title,
			description,
			sitelogo: sitelogo ?? '',
		} );
	};

	let logoImage;

	// Set the image URL once retrieved.
	if ( mediaItemData && logoUrl !== mediaItemData.url ) {
		setLogoUrl( mediaItemData.url );
	}

	// Show a spinner if the image hasn't been retrieved yet.
	if ( sitelogo === undefined ) {
		logoImage = <Spinner />;
	}

	// Show the image once retrieved.
	if ( !! logoUrl ) {
		logoImage = <img src={ logoUrl } alt={ __( 'website logo', 'nextgen' ) } />;
	}

	return (
		<PublishGuideItem
			title={ __( 'Add your site info & brand', 'nextgen' ) }
			text={ __( 'Whats the name of your site and whats its purpose? Add a logo if you have one.', 'nextgen' ) }
			eidSection="branding"
			{ ...props }
		>
			{ ! isOpen &&
				<Button
					isLink
					className="publish-guide-popover__link"
					onClick={ () => toggleOpen() }
				>
					{ __( 'Edit', 'nextgen' ) } <Icon icon={ chevronDown } size="20" />
				</Button>
			}

			{ isOpen && <div className="publish-guide-popover__item__inner-content">
				<TextControl
					label={ __( 'Site title', 'nextgen' ) }
					value={ title }
					onChange={ ( newValue ) => setTitle( newValue ) }
					data-eid={ `${ EID_PREFIX }.guide/branding.input_title.text` }
				/>
				<TextControl
					label={ __( 'Site description', 'nextgen' ) }
					value={ description }
					onChange={ ( newValue ) => setDescription( newValue ) }
					data-eid={ `${ EID_PREFIX }.guide/branding.input_title.text` }
				/>

				<div className="publish-guide-popover__upload">
					{ noticeUI }
					<MediaUploadCheck>
						<MediaUpload
							title={ __( 'Upload site logo', 'nextgen' ) }
							onSelect={ onSelectLogo }
							allowedTypes={ ALLOWED_MEDIA_TYPES }
							render={ ( { open } ) => (
								<div>
									<Button
										className={ classnames(
											'publish-guide-popover__upload-button',
											{ 'publish-guide-popover__upload-button--preview': sitelogo }
										) }
										onClick={ open }
										aria-label={
											! sitelogo
												? null
												: __( 'Upload site logo', 'nextgen' )
										}
									>
										{ !! sitelogo && logoImage }
										{ ! sitelogo && __( 'Upload site logo', 'nextgen' ) }
									</Button>
									<DropZone onFilesDrop={ onDropImage } />
								</div>
							) }
							value={ sitelogo }
							data-eid={ `${ EID_PREFIX }.guide/branding.upload_logo.click` }
						/>
					</MediaUploadCheck>

					{ !! sitelogo && logoImage && (
						<MediaUploadCheck>
							<MediaUpload
								title={ __( 'Upload site logo', 'nextgen' ) }
								onSelect={ onSelectLogo }
								allowedTypes={ ALLOWED_MEDIA_TYPES }
								render={ ( { open } ) => (
									<Button onClick={ open } isSecondary data-eid={ `${ EID_PREFIX }.guide/branding.replace_logo.click` }>
										{ __( 'Replace Logo', 'nextgen' ) }
									</Button>
								) }
							/>
						</MediaUploadCheck>
					) }

					{ !! sitelogo && (
						<MediaUploadCheck>
							<Button onClick={ deleteLogo } isTertiary isDestructive data-eid={ `${ EID_PREFIX }.guide/branding.remove_logo.click` }>
								{ __( 'Remove Logo', 'nextgen' ) }
							</Button>
						</MediaUploadCheck>
					) }
				</div>

				<Button
					isPrimary
					className={ classnames(
						'publish-guide-popover__button',
						{ 'is-busy': logoIsSaving }
					) }
					disabled={ logoIsSaving }
					onClick={ () => {
						setLogoIsSaving( true );
						saveSettings().then( () => {
							toggleComplete();
							setLogoIsSaving( false );
						} );
					} }
					data-eid={ `${ EID_PREFIX }.guide/branding.save.click` }
				>
					{ __( 'Save', 'nextgen' ) }
				</Button>
				<Button
					className="publish-guide-popover__do-this-later"
					onClick={ () => toggleOpen() }
					data-eid={ `${ EID_PREFIX }.guide/branding.close.click` }
				>
					{ __( 'Do this later', 'nextgen' ) }
				</Button>
			</div> }
		</PublishGuideItem>
	);
};

export default compose( [
	withNotices,
] )( SiteInfoGuide );
