/* global nextgenPublishGuideDefaults */

/**
 * External dependencies
 */
import { ChecklistIcon } from '@godaddy-wordpress/coblocks-icons';
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { ComplementaryAreaMoreMenuItem } from '@wordpress/interface';
import { registerPlugin } from '@wordpress/plugins';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch } from '@wordpress/data';
import { Fragment, Component, createPortal } from '@wordpress/element';

import {
	Button,
	Icon,
	Popover,
	DropdownMenu,
} from '@wordpress/components';
import { check, close, moreVertical, comment } from '@wordpress/icons';

/**
 * Internal dependencies
 */
import './store';
import PublishGuideList from './publish-guide-list';
import PublishGuideTooltip from './publish-guide-tooltip';
import SiteDesignGuide from './publish-guide-items/site-design';
import SiteContentGuide from './publish-guide-items/site-content';
import SiteInfoGuide from './publish-guide-items/site-info';
import { AddDomainGuide } from './publish-guide-items/add-domain';
import SuccessView from './success-view';
import './style.scss';
import { constant } from '@wpex/nextgen-common'; // eslint-disable-line import/no-unresolved
const { EID_PREFIX } = constant;

const wpUserData = JSON.parse( window.localStorage.getItem( 'WP_DATA_USER_' + nextgenPublishGuideDefaults.userId ) );

export class PublishGuide extends Component {
	parentElement;
	tooltipTargetElement;
	tooltipAutoHideTimeout;

	constructor() {
		super( ...arguments );

		this.state = {
			confettis: false,
			homeUrl: window.location.hostname,
			isCompleted: false,
			isOpen: false,
			isTooltipOpen: false,
			isFeedbackModalOpen: false,
			title: '',
			tagline: '',
			selectedItem: 0,
			tooltip: null,
			tooltips: {
				siteDesign: {
					title: __( 'Design Customizer', 'nextgen' ),
					description: __( 'Click here to update your colors, fonts and feel of your site.', 'nextgen' ),
					refID: 'nextgen-site-design-pinned-icon',
					nextTooltip: 'publishGuideHere',
					collapseSidePanel: true,
					autoHide: false,
				},
				siteContent: {
					title: __( 'Site Contents', 'nextgen' ),
					description: __( 'Click here to add and manage the pages for your site.', 'nextgen' ),
					refID: 'nextgen-site-content-pinned-icon',
					nextTooltip: 'publishGuideHere',
					collapseSidePanel: true,
					autoHide: false,
				},
				publishGuideHere: {
					title: __( 'Getting Started Guide', 'nextgen' ),
					description: __( 'Once you\'re done selecting your site style and colors, click here to continue publishing your site.', 'nextgen' ),
					refID: 'nextgen-publish-guide-trigger-button',
					collapseSidePanel: false,
					autoHide: 10000,
				},
			},
		};

		this.prepareDOM();
	}

	componentDidMount() {
		if ( this.props.isOpen ) {
			setTimeout( () => this.setState( { isOpen: true } ), 2000 );
		}

		// Hide the welcomeGuide if no localStorage is present (normally on first load).
		if ( ! wpUserData && this.props.isEditPostFeatureActive( 'welcomeGuide' ) ) {
			this.props.toggleEditPostFeature( 'welcomeGuide' );
		}
	}

	componentWillUnmount() {
		this.cleanDOM();

		clearTimeout( this.tooltipAutoHideTimeout );

		if ( ! this.tooltipTargetElement ) {
			return null;
		}

		this.unbindTooltipTargetElementEvent();
		this.tooltipTargetElement.classList.remove( 'nextgen-button-pulsating' );
	}

	/**
	 * Prepares the DOM for the Publish Guide to be rendered.
	 */
	prepareDOM() {
		/* istanbul ignore else */
		if ( ! this.parentElement ) {
			this.parentElement = document.createElement( 'div' );
			document.body.appendChild( this.parentElement );
		}
		this.node = document.createElement( 'div' );
		this.parentElement.appendChild( this.node );
	}

	/**
	 * Removes the specific mounting point for the Publish Guide from the DOM.
	 */
	cleanDOM() {
		this.parentElement.removeChild( this.node );
	}

	/**
	 * Toggle the success view and trigger the confettis animation on first display
	 */
	toggleSuccess = () => {
		this.setState(
			{
				confettis: false,
				isCompleted: true,
			},
			() => {
				// Confettis needs this delay to trigger
				setTimeout( () => this.setState( { confettis: true } ), 100 );
			} );
	};

	/**
	 * Toggle the Popover
	 */
	togglePublishGuideOpen = () => {
		this.setState( { isOpen: ! this.state.isOpen } );
		this.props.togglePublishGuideOpen();
	};

	/**
	 * Toggle the Feedback Modal
	 */
	triggerToggleFeedback = () => {
		// eslint-disable-next-line chai-friendly/no-unused-expressions
		window._trfq?.push( [ 'cmdLogEvent', 'click', EID_PREFIX + 'guide/more_menu.feedback.click' ] );
		this.props.toggleFeedbackModal();
		this.togglePublishGuideOpen();
	};

	/**
	 * Trigger the Tooltip
	 *
	 * @param {string} el - Name of the key to display
	 */
	triggerTooltip = ( el ) => {
		const tooltip = this.state.tooltips[ el ];

		if ( ! tooltip ) {
			return null;
		}

		if ( this.tooltipTargetElement ) {
			this.closeTooltip();
		}

		this.tooltipTargetElement = document.getElementById( tooltip.refID );

		if ( ! this.tooltipTargetElement ) {
			return null;
		}

		this.tooltipTargetElement.addEventListener( 'click', this.handleTooltipIconClick );
		this.tooltipTargetElement.classList.add( 'nextgen-button-pulsating' );

		if ( tooltip.collapseSidePanel ) {
			this.props.closeGeneralSidebar();
		}

		if ( tooltip.autoHide ) {
			this.tooltipAutoHideTimeout = setTimeout( () => this.closeTooltip(), tooltip.autoHide );
		}

		this.setState( {
			isOpen: false,
			tooltip: {
				...tooltip,
				anchorRect: this.tooltipTargetElement.getBoundingClientRect(),
			},
		} );
	};

	/**
	 * Handle click actions on the element associated with the Tooltip
	 */
	handleTooltipIconClick = () => {
		const { nextTooltip } = this.state.tooltip;

		this.tooltipTargetElement.classList.remove( 'nextgen-button-pulsating' );
		this.unbindTooltipTargetElementEvent();

		if ( nextTooltip ) {
			setTimeout( () => this.triggerTooltip( nextTooltip ), 200 );
			return;
		}

		this.closeTooltip();
	};

	/**
	 * Close the Tooltip
	 */
	closeTooltip = () => {
		clearTimeout( this.tooltipAutoHideTimeout );
		this.setState( { tooltip: null } );

		if ( ! this.tooltipTargetElement ) {
			return null;
		}

		this.unbindTooltipTargetElementEvent();
		this.tooltipTargetElement.classList.remove( 'nextgen-button-pulsating' );

		this.tooltipTargetElement = null;
	};

	/**
	 * Remove the eventListener on the element associated with the Tooltip
	 */
	unbindTooltipTargetElementEvent = () => this.tooltipTargetElement.removeEventListener( 'click', this.handleTooltipIconClick );

	render() {
		// @todo Fix Publish Guide style for mobile before enabling it
		if ( this.props.isMobile ) {
			return null;
		}

		const {
			isActive,
			shiftedDisplay,
			setPublishGuide,
		} = this.props;

		const {
			confettis,
			homeUrl,
			isCompleted,
			isOpen,
			tooltip,
		} = this.state;

		return (
			<Fragment>
				<ComplementaryAreaMoreMenuItem
					scope={ window.nextgen?.editSite ? 'nextgen/edit-site' : 'core/edit-post' }
					icon={ isActive ? check : <svg /> }
					className={ 'publish-guide-icon ' }
					onClick={ () => setPublishGuide( ! isActive ) }
				>
					{ __( 'Getting Started Guide', 'nextgen' ) }
				</ComplementaryAreaMoreMenuItem>

				{ isActive && createPortal(
					<div className={ classnames(
						'publish-guide-trigger',
						{ 'is-shifted': shiftedDisplay }
					) }>

						{ isOpen && (
							<Popover
								className="publish-guide-popover"
								position="top left"
								focusOnMount="container">
								<div className="publish-guide-popover__header components-modal__header">
									<h1 className="components-modal__header-heading">
										{ __( 'Getting Started Guide', 'nextgen' ) }
									</h1>
									{ /* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */ }
									<div onClick={ () => window._trfq?.push( [ 'cmdLogEvent', 'click', EID_PREFIX + '.guide/more_menu.click' ] ) }>
										<DropdownMenu
											icon={ moreVertical }
											className="publish-guide-popover__more-dropdown"
											controls={ [
												{
													title: __( 'Give feedback', 'nextgen' ),
													icon: comment,
													onClick: this.triggerToggleFeedback,
												},
												{
													title: __( 'Hide this guide', 'nextgen' ),
													icon: <svg fill="none" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path clipRule="evenodd" d="m18.6548 6.46091-2.0811 2.08903-.5618.55391-7.32742 7.32745-.56973.5697-1.65381 1.6538-1.11573-1.1157 1.5272-1.5272c-1.08408-1.0129-2.03363-2.2869-2.70624-3.6637l-.16617-.3482.16617-.3482c1.40851-2.8803 4.45501-5.98218 7.83383-5.98218 1.274 0 2.6192.46686 3.8853 1.32938l1.6538-1.65382zm-7.826 4.33629c-.451.4511-.5855 1.0999-.4194 1.6776l2.0812-2.0811c-.5698-.1662-1.2266-.0396-1.6618.4035zm2.8487-1.59839c-.4986-.30861-1.0762-.4827-1.6776-.4827-.8704 0-1.6855.34026-2.30267.96539-1.06825 1.0762-1.2186 2.73-.48269 3.9881l-1.2186 1.2186c-.87043-.7992-1.6459-1.7962-2.22355-2.8882 1.28982-2.41346 3.76658-4.74779 6.22751-4.74779.8783 0 1.82.31652 2.7379.88626zm4.5896.00791c.6172.75965 1.1474 1.58258 1.5668 2.44508l.1662.3482-.1662.3482c-1.4085 2.8803-4.455 5.9822-7.8338 5.9822-.6964 0-1.4006-.1345-2.11281-.3957l-.34026-.1266 1.26607-1.2581c.4036.1187.8071.1978 1.1949.1978 2.4609 0 4.9456-2.3343 6.2354-4.7478-.3244-.6093-.7122-1.1869-1.1474-1.7329l1.1237-1.12368z" fill="currentColor" fillRule="evenodd" /></svg>,
													onClick: () => {
														// eslint-disable-next-line chai-friendly/no-unused-expressions
														window._trfq?.push( [ 'cmdLogEvent', 'click', EID_PREFIX + '.guide/more_menu.hide.click' ] );
														this.togglePublishGuideOpen();
													},
												},
											] }
										/>
									</div>
								</div>

								<PublishGuideList onListComplete={ this.toggleSuccess }>
									<SiteInfoGuide />
									<SiteDesignGuide setTooltip={ this.triggerTooltip } />
									<SiteContentGuide setTooltip={ this.triggerTooltip } />
									<AddDomainGuide />
								</PublishGuideList>

								{ isCompleted &&
									<SuccessView
										homeurl={ homeUrl }
										confettis={ confettis }
										onTriggerClosePopover={ this.togglePublishGuideOpen }
										onTriggerGiveFeedback={ this.triggerToggleFeedback }
									/>
								}
							</Popover>
						) }

						<Button
							className={ classnames(
								'publish-guide-trigger__button',
								{ 'is-opened': isOpen }
							) }
							id="nextgen-publish-guide-trigger-button"
							aria-label={ __( 'Publish Guide', 'nextgen' ) }
							isPrimary
							onClick={ this.togglePublishGuideOpen }
							data-eid={ isOpen ? `${ EID_PREFIX }.guide.close.click` : `${ EID_PREFIX }.guide.open.click` }
						>
							<span className="publish-guide-trigger__button__icon-check">
								<Icon icon={ ChecklistIcon } />
							</span>
							<span className="publish-guide-trigger__button__icon-close">
								<Icon icon={ close } />
							</span>

							<div className="publish-guide-trigger__button__pulse">
								<span />
								<span />
								<span />
							</div>
						</Button>
					</div>,
					this.node
				) }

				{ tooltip && createPortal(
					<PublishGuideTooltip
						tooltip={ this.state.tooltip }
						closeCallback={ this.closeTooltip } />,
					this.node
				) }
			</Fragment>
		);
	}
}

/* istanbul ignore next */
registerPlugin( 'nextgen-publish-guide', {
	render: compose( [

		withSelect( ( select ) => {
			const isLayoutSelectorActive = select( 'coblocks/template-selector' ).isTemplateSelectorActive?.() ?? false;
			const { getActiveComplementaryArea } = select( 'core/interface' );
			const { isPublishGuideOpen } = select( 'nextgen/publish-guide' );

			const { nextgen_publish_guide_active: isPublishGuideActive } = select( 'core' ).getEditedEntityRecord( 'root', 'site' );

			// Flickering should be fixed once this issue is merged
			// https://github.com/WordPress/gutenberg/pull/26389
			return {
				isEditPostFeatureActive: select( 'core/edit-post' ).isFeatureActive,
				isActive: isPublishGuideActive && ! isLayoutSelectorActive,
				isOpen: isPublishGuideOpen(),
				isMobile: select( 'core/viewport' ).isViewportMatch( '< medium' ),
				shiftedDisplay: getActiveComplementaryArea( 'nextgen/edit-site' ) || getActiveComplementaryArea( 'core/edit-post' ),
			};
		} ),

		withDispatch( ( dispatch ) => {
			const { closeGeneralSidebar } = dispatch( window.nextgen?.editSite ? 'nextgen/edit-site' : 'core/edit-post' );
			const { toggleFeedbackModal } = dispatch( 'nextgen/feedback-modal' );
			const { togglePublishGuideOpen } = dispatch( 'nextgen/publish-guide' );

			const setPublishGuide = ( newValue ) => {
				dispatch( 'core' ).saveEntityRecord( 'root', 'site', { nextgen_publish_guide_active: newValue } );
			};

			return {
				closeGeneralSidebar,
				setPublishGuide,
				toggleFeedbackModal,
				togglePublishGuideOpen,
				toggleEditPostFeature: dispatch( 'core/edit-post' ).toggleFeature,
			};
		} ),

	] )( PublishGuide ),
} );
