/**
 * WordPress dependencies
 */
import { Component } from '@wordpress/element';
import { Icon, Popover } from '@wordpress/components';
import { close } from '@wordpress/icons';

export default class PublishGuideTooltip extends Component {
	render() {
		const { tooltip, closeCallback } = this.props;

		return (
			<Popover
				anchorRect={ tooltip.anchorRect }
				className={ `publish-guide-tooltip${ ! tooltip.nextTooltip ? ' publish-guide-tooltip--next' : '' }` }
				noArrow={ false }
				position="bottom left">

				<h3 className="publish-guide-tooltip__title">
					{ tooltip.title }
				</h3>
				<div className="publish-guide-tooltip__description">
					{ tooltip.description }
				</div>

				<Icon icon={ close } className="publish-guide-tooltip__close" onClick={ closeCallback } />

			</Popover>
		);
	}
}
