/**
 * WordPress dependencies
 */
import { plugins, registerStore, use } from '@wordpress/data';

const DEFAULT_STATE = {
	publishGuideOpen: true,
};

export const actions = {
	togglePublishGuideOpen: () => ( { type: 'TOGGLE_PUBLISH_GUIDE_OPEN' } ),
};

export const reducer = ( state = DEFAULT_STATE, action ) => {
	use( plugins.persistence, state );

	if ( action.type === 'TOGGLE_PUBLISH_GUIDE_OPEN' ) {
		return {
			...state,
			publishGuideOpen: ! state.publishGuideOpen,
		};
	}

	return state;
};

export const selectors = {
	isPublishGuideOpen: ( state ) => state.publishGuideOpen,
};

const store = registerStore( 'nextgen/publish-guide', {
	reducer,
	actions,
	selectors,
	persist: [ 'publishGuideOpen' ],
} );

export default store;
